import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class BikesService {
  constructor(private http: HttpClient) {}

  bikes = new BehaviorSubject<any[]>(null);

  getAll() {
    return this.http.get(baseUrl + '/bike').subscribe((res) => {
      if (res['data']) {
        this.bikes.next(res['data']);
      }
    });
  }

  getOne(itemId) {
    return this.http.get(baseUrl + `/bike/${itemId}`);
  }

  addOne(form) {
    return this.http.post(baseUrl + '/bike', form);
  }

  updateOne(form, itemId) {
    return this.http.put(baseUrl + `/bike/${itemId}`, form);
  }

  deleteOne(itemId) {
    return this.http.delete(baseUrl + `/bike/${itemId}`);
  }

  addBikeToStore(bikeId, storeId) {
    return this.http.post(
      baseUrl + `/bike/addBikeToStore/${bikeId}/${storeId}`,
      {}
    );
  }

  activateOne(itemId) {
    return this.http.get(baseUrl + `/bike/activate/${itemId}`);
  }

  deactivateOne(itemId) {
    return this.http.get(baseUrl + `/bike/deactivate/${itemId}`);
  }

  soldOne(itemId) {
    return this.http.get(baseUrl + `/bike/sold/${itemId}`);
  }

  availableOne(itemId) {
    return this.http.get(baseUrl + `/bike/available/${itemId}`);
  }

  addDiscount(form, itemId) {
    return this.http.post(baseUrl + `/bike/discount/add/${itemId}`, form);
  }

  deleteDiscount(itemId) {
    return this.http.delete(baseUrl + `/bike/discount/delete/${itemId}`);
  }

  bestSelling() {
    return this.http.get(baseUrl + `/bike/bestSelling`);
  }
}
